import React from 'react';
import { LazilyLoadFactory } from '../lib/LazilyLoad';

class Load_G2 extends React.Component {

    componentDidMount() {
        console.log('Load_G2 props:', this.props.g2);

        const G2 = this.props.g2;

        const data = [
            {action: '', visitor: 500},
            {action: '', visitor: 400},
            {action: '', visitor: 300},
            {action: '', visitor: 200},
            {action: '', visitor: 100}
        ];
        const chart = new G2.Chart({
            id: 'c1',
            width : 800,
            height : 400
        }); // 

        chart.source(data); // 
        chart.interval().position('action*visitor').color('action'); // 
        chart.render();
    }

    render() {
        return (
            <div>
                Hello G2
                <div id="c1"></div>
            </div>
        );
    }

};

export default LazilyLoadFactory(Load_G2, {
    // g2: () => require('es6-promise?g2!g2')(),
    g2: () => require('g2')(),
});


